<template>
  <div class="the-aggregation">
    <Trade/>
  </div>
</template>
<script>
import Trade from '../components/trade/index'
import NCWeb3 from "../assets/js/web3"
import { Empty } from 'vant'
export default {
  name: '',
  components: {
    Trade,
    [Empty.name]:Empty,
  },
  props: {},
  data () {
    return {
      
    }
  },
  computed: { 
  },
  watch: {},
  created () {
  },
  mounted () {
    NCWeb3.Init(addr => {
	    // console.log(addr)
      this.getWeb3()
		})
  },
  beforeDestroy() {
      
  },
  methods: {
    async getWeb3(){
      // console.log(ethereum.networkVersion)
      // console.log('MetaMask installed ? ', ethereum.isMetaMask)
      // 请求用户授权 解决web3js无法直接唤起Meta Mask获取用户身份
      // const enable = await ethereum.enable();
      // console.log(enable[0])
      // 授权获取账户
      let accounts = await web3.eth.getAccounts();
      // web3.eth.getAccounts().then((e)=>{console.log(e)})
      // 取第一个账户
      let myAccount = accounts[0];
      // console.log(myAccount, 1);
      // 返回指定地址账户的余额
      let balance = await web3.eth.getBalance(myAccount)
      this.balance = balance
      this.balance = Number(window.web3.utils.fromWei(this.balance.toString())).toFixed(4);
      // let net = await web3.eth.net
      // console.log('net',net)
      // web3.eth.net.isListening().then(console.log);
      // console.log('Listening',isListening)
      
    }
  }
}
</script>
