<template>
  <div class="trade-cont">
    <div class="trade-change">
      <div class="top-menu">
        <div class="top-menu-left">From</div>
        <div class="top-menu-right">Balance：{{coinBalance1}} {{curCoin1}}</div>
      </div>
      <div class="coin-cont coin-line1" id="coreCoin1"> 
        <div class="coin-line1-right">
          <van-field v-model="coinNum1" type="number" placeholder="0"/>
        </div>
        <div class="max-num" @click="getMax()">MAX</div>
        <div class="coin-line1-left" @click="coinList1"><img class="logo-url" :src="logoURI1" alt=""><span>{{curCoin1}}</span></div>
      </div>
      <div class="switch-input">
        <img @click="changeTrade()" src="../../assets/img/icon-light.svg" alt="">
      </div>
      <div class="top-menu">
        <div class="top-menu-left">To</div>
        <div class="top-menu-right">Balance：{{coinBalance2}} {{curCoin2}}</div>
      </div>
      <div class="coin-cont coin-line2" id="coreCoin2">
        <div class="coin-line1-right">
          <van-field v-model="coinNum2" type="number" placeholder="0" disabled />
        </div>
        <div class="coin-line1-left" @click="coinList2"><img class="logo-url" :src="logoURI2" alt=""><span>{{curCoin2}}</span></div>
      </div>
      <div class="trade-data">
        <div class="trade-data-left">Rate</div>
        <div class="trade-data-right">
          <p>1 {{curCoin1}} ≈ <span>{{tradeData1}}</span> {{curCoin2}}</p>
          <p>1 {{curCoin2}} ≈ <span>{{tradeData2}}</span> {{curCoin1}}</p>
        </div>
      </div>
      <div class="trade-data">
        <div class="trade-data-left">USD Price</div>
        <div class="trade-data-right">
          <p>1 {{curCoin1}} ≈ <span>${{Number(priceData1).toFixed(4)}}</span></p>
          <p>1 {{curCoin2}} ≈ <span>${{Number(priceData2).toFixed(4)}}</span></p>
        </div>
      </div>
    </div>
    <!-- <div class="trade-data">
      <div class="trade-data-left">Estimated Fee</div>
      <div class="trade-data-right">
        <span>${{feeData}}</span>
      </div>
    </div>
    <div class="trade-data">
      <div class="trade-data-left">You Save</div>
      <div class="trade-data-right">
        <em>≈ ${{saveData}}</em>
      </div>
    </div> -->
    <div class="btn-swap" v-if="approveOn">
      <van-button color="#0049dc" :loading = 'arrovestatus' :disabled = 'arrovestatus' plain @click="beforApprove()" loading-text="Approving...">Approve</van-button>
    </div>
    <div class="btn-swap" v-else>
      <van-button color="#0049dc" plain @click="enterSwap = true,enterAlert()">Swap</van-button>
    </div>
    <van-popup v-model="enterSwap" class="enter-swap" closeable>
      <div class="enter-swap-cont">
        <h4>Confirm Swap</h4>
        <div class="trade-data" v-if="changeOn == true">
          <div class="trade-data-left"><span>{{coinNum1}}</span></div>
          <div class="trade-data-right">
            <p><em>{{curCoin1}}</em></p>
          </div>
        </div>
        <div class="trade-data" v-else>
          <div class="trade-data-left"><span>{{coinNum2}}</span></div>
          <div class="trade-data-right">
            <p><em>{{curCoin2}}</em></p>
          </div>
        </div>

        <div class="trade-data">
          <div class="trade-data-left"><img class="icon-down" src="../../assets/img/icon-down.svg" alt=""></div>
        </div>
        <div class="trade-data" v-if="changeOn == true">
          <div class="trade-data-left"><span>{{coinNum2}}</span></div>
          <div class="trade-data-right">
            <p><em>{{curCoin2}}</em></p>
          </div>
        </div>
        <div class="trade-data" v-else>
          <div class="trade-data-left"><span>{{coinNum1}}</span></div>
          <div class="trade-data-right">
            <p><em>{{curCoin1}}</em></p>
          </div>
        </div>
        <div class="trade-data">
          <div class="trade-data-left">Rate</div>
          <div class="trade-data-right">
            <p>1 {{curCoin1}} ≈ <span>{{tradeData1}}</span> {{curCoin2}}</p>
            <p>1 {{curCoin2}} ≈ <span>{{tradeData2}}</span> {{curCoin1}}</p>
          </div>
        </div>
        <div class="trade-data">
          <div class="trade-data-left">Minimum received</div>
          <div class="trade-data-right">
            <p>{{(Number(coinNum2) - (Number(coinNum2) *sliderCoin)).toFixed(4)}} {{curCoin2}}</p>
          </div>
        </div>
        <div class="trade-data">
          <div class="trade-data-left">Price Impact</div>
          <div class="trade-data-right">
            <span>{{Number(sliderCoin)*100}}%</span>
          </div>
        </div>
        <!-- <div class="trade-data">
          <div class="trade-data-left">Transaction cost</div>
          <div class="trade-data-right">
            <p>1 {{curCoin2}}</p>
          </div>
        </div> -->
        <van-button color="#0049dc" plain @click="judgeSwap()" :loading = 'swapstatus' :disabled = 'swapstatus' loading-text="In exchange...">Confirm Swap</van-button>
      </div>
    </van-popup>
    <WalletList ref="walletChild" @coin-type1 = 'chioseCoin1' @coin-type2 = 'chioseCoin2'/>
  </div>
</template>
<script>
import { Field } from 'vant'
import { Button } from 'vant'
import { Popup } from 'vant'
import { Toast } from "vant";
import WalletList from './walletlist'
import NCWeb3 from "../../assets/js/web3"
import { configabi } from '../../abi/configabi'
import indexApi from "../../api/index"
import { eventBus } from '../../main'
// import element from '../../assets/js/element'
export default {
  name: '',
  components: { 
    [Field.name]:Field,
    [Button.name]:Button,
    [Popup.name]:Popup,
    WalletList,
  },
  props: {},
  data () {
    return {
      active: 1,
      curCoin1: '',
      curCoin2: '',
      selectCoin1:'',
      selectCoin2:'',
      coinBalance1: 0,
      coinBalance2: 0,
      selectBalance1:0,
      selectBalance2:0,   
      logoURI1: '',
      logoURI2: '',
      selectlogoURI1: '',
      selectlogoURI2: '',
      curAddr1:'',
      curAddr2:'',
      balance: 20,
      coinNum1: 1,
      coinNum2: 0,
      tradeData1: 0,
      tradeData2: 0,
      priceData1: 0,
      priceData2: 0,
      feeData: 0.5,
      saveData: 0.36,
      changeOn: true,
      changeUp: false,
      enterSwap: false,
      approveOn: true,
      arrovestatus: false,
      swapstatus:false,
      swapGas:'0.15',
      rateList:[],
      sliderCoin: "0.005",
      datatimer:null,
      token1_amount:''
    }
  },
  computed: { 
  },
  watch: { 
    coinNum1(curVal, oldVal) {
    // 实现input连续输入，只发一次请求
      clearTimeout(this.timeout)
      this.timeout = setTimeout(() => {
        // console.log(this.coinNum1)
        this.getTrade()
      }, 500)
    }
  },
  created() {
    const cherryAddr = '0x87a50c67d9b8fcea16d132b0bdbb142b96e35b6b'
    this.cherryAddr = cherryAddr
    const cherryRouter = '0x865bfde337C8aFBffF144Ff4C29f9404EBb22b15'
    this.cherryRouter = cherryRouter
  },
  mounted() {
    NCWeb3.Init(addr => {
      this.setTrade()
      this.getTokkenFee()
		})
    eventBus.$on("curGasPrice", (gas) => {
      // console.log(gas)
      this.swapGas = gas
      // console.log(this.swapGas)
    });
    eventBus.$on("curcoinSlide", (slide) => {
      this.sliderCoin = slide
      // console.log(slide)
    });
    eventBus.$on("curSwapList", (list) => {
      console.log(list)
    });
    // 定时器刷新数据
    this.datatimer = 
      setInterval(()=>{
        this.getTrade();
      },10000)
  },
  beforeDestroy() {
    clearInterval(this.datatimer);
    this.datatimer = null;
  },
  destroyed() {
    clearInterval(this.datatimer);
    this.datatimer = null;
  },
  inject:[ 'reload'],
  methods: {
    // 判断是否需要授权
    getCurNetbalance(){
      if(this.curCoin1 == 'OKT'){
        this.approveOn = false
      }else{
        this.approveOn = true
      } 
    },
    // 获取初始化数据
    getInitial(){
      // console.log(this.$refs.walletChild)
      // console.log(this.$refs.walletChild.walletList)
      this.selectCoin1 = this.$refs.walletChild.walletList[0].symbol
      this.selectCoin2 = this.$refs.walletChild.walletList[1].symbol
      
      this.selectBalance1 = this.$refs.walletChild.walletList[0].balance
      this.selectBalance2 = this.$refs.walletChild.walletList[1].balance
      this.selectlogoURI1 = this.$refs.walletChild.walletList[0].logoURI
      this.selectlogoURI2 = this.$refs.walletChild.walletList[1].logoURI
      
      this.curCoin1 = this.selectCoin1
      this.curCoin2 = this.selectCoin2
      this.coinBalance1 = this.selectBalance1
      this.coinBalance2 = this.selectBalance2
      this.logoURI1 = this.selectlogoURI1
      this.logoURI2 = this.selectlogoURI2
    },
    // 筛选数据1
    getAddr1(){
      let curName1 = [this.selectCoin1]
      // console.log(curName1)
      let result1 = this.$refs.walletChild.walletList.filter((item)=>{
        return curName1.some(e=>(e === item.symbol))
      })
      let selectAddr1 = result1[0].tokenAddr
      this.selectAddr1 = selectAddr1
      this.curAddr1 = this.selectAddr1
      // console.log(this.curAddr1)
    },
    // 筛选数据2
    getAddr2(){
      let curName2 = [this.selectCoin2]
      // console.log(curName2)
      let result2 = this.$refs.walletChild.walletList.filter((item)=>{
        return curName2.some(e=>(e === item.symbol))
      })
      let selectAddr2 = result2[0].tokenAddr
      this.selectAddr2 = selectAddr2
      this.curAddr2 = this.selectAddr2
      // console.log(this.curAddr2)
    },
    setTrade(){
      if (typeof window.web3 !== 'undefined') {
        window.PgoMixContract = new window.web3.eth.Contract(configabi.PgoMix.abi,configabi.PgoMixAddress)
      }  
    },
    enterAlert(){
      this.swapstatus = false
    },
    // 获取汇率
    getTrade(){
      let dataRate = {
        "from": this.curAddr1,
        "to": this.curAddr2,
        "amountIn": window.web3.utils.toWei(this.coinNum1.toString())
      }
      this.dataRate = dataRate
      // console.log('--当前兑换的地址---', this.dataRate)
      this.getCurNetbalance()
      indexApi.getPolymerize(this.dataRate).then(res =>{
        if(res.data.routeList[0].path.tick.length > 1){
          if(res.data.routeList[0].path.tick[1].charge == undefined){
            // console.log('----')
            let tickList = res.data.routeList[0].path.tick[1]
            this.tickList = tickList
            this.tickList = this.tickList.filter(item => {
              if(item.ex == "cherry"){
                return item
              }
            })
            // console.log(this.tickList)
            let fromToRate = 1/this.tickList[0].charge
            this.fromToRate = fromToRate
            let toFromRate = this.tickList[0].charge
            this.toFromRate = toFromRate
            this.coinNum2 = (this.fromToRate*0.997).toFixed(6)
            this.tradeData1 = ((this.fromToRate*0.997)/this.coinNum1).toFixed(6)
            this.tradeData2 = ((this.toFromRate*0.997)*this.coinNum1).toFixed(6)
            this.priceData1 = Number(res.data.token0Usdt).toFixed(4)
            this.priceData2 = Number((res.data.token0Usdt / this.fromToRate)*this.coinNum1).toFixed(4)
            this.token1_amount = Number(window.web3.utils.fromWei((this.tickList[0].token1_amount).toString())).toFixed(6)
            if(Number(this.coinNum2) > Number(this.token1_amount)){
              this.coinNum2 = Number(this.token1_amount).toFixed(6)
              Toast('Current pool maximum convertible：' + token1_amount + this.curCoin2)
            }
            // console.log('%%%%%%%%%', token1_amount)
          }else if(res.data.routeList[0].path.tick[1].charge !== undefined){
            // console.log('----')
            let fromToRate = res.data.routeList[0].path.tick[1].charge
            this.fromToRate = fromToRate
            let toFromRate = 1/res.data.routeList[0].path.tick[1].charge
            this.toFromRate = toFromRate
            this.coinNum2 = (this.fromToRate*0.997).toFixed(6)
            this.tradeData1 = ((this.fromToRate*0.997)/this.coinNum1).toFixed(6)
            this.tradeData2 = ((this.toFromRate*0.997)*this.coinNum1).toFixed(6)
            this.priceData1 = Number(res.data.token0Usdt).toFixed(4)
            this.priceData2 = Number((res.data.token0Usdt / this.fromToRate)*this.coinNum1).toFixed(4)
            this.token1_amount = Number(window.web3.utils.fromWei((res.data.routeList[0].path.tick[1][0].token1_amount).toString())).toFixed(6)
            // console.log('%%%%%%%%%', token1_amount)
            if(Number(this.coinNum2) > Number(this.token1_amount)){
              this.coinNum2 = Number(this.token1_amount).toFixed(6)
              Toast('Current pool maximum convertible：' + this.token1_amount + this.curCoin2)
            }
          }
        }else{
          //  console.log('长度为1')
          if(res.data.routeList[0].path.tick[0].charge == undefined){
            // console.log('----')
          //  console.log('长度为1-1')
            let tickList = res.data.routeList[0].path.tick[0]
            this.tickList = tickList
            this.tickList = this.tickList.filter(item => {
              if(item.ex == "cherry"){
                return item
              }
            })
            // console.log(this.tickList)
            let fromToRate = this.tickList[0].charge
            this.fromToRate = fromToRate
            let toFromRate = 1/this.tickList[0].charge
            this.toFromRate = toFromRate
            this.coinNum2 = (this.fromToRate*0.997).toFixed(6)
            this.tradeData1 = ((this.fromToRate*0.997)/this.coinNum1).toFixed(6)
            this.tradeData2 = ((this.toFromRate*0.997)*this.coinNum1).toFixed(6)
            this.priceData1 = Number(res.data.token0Usdt).toFixed(4)
            this.priceData2 = Number((res.data.token0Usdt / this.fromToRate)*this.coinNum1).toFixed(4)
            this.token1_amount = Number(window.web3.utils.fromWei((this.tickList[0].token1_amount).toString())).toFixed(6)
            if(Number(this.coinNum2) > Number(this.token1_amount)){
              this.coinNum2 = Number(this.token1_amount).toFixed(6)
              Toast('Current pool maximum convertible：' + this.token1_amount + this.curCoin2)
            }
            // console.log('%%%%%%%%%', token1_amount)
          }else{
            // console.log('----')
            let fromToRate = res.data.routeList[0].path.tick[0].charge
            this.fromToRate = fromToRate
            let toFromRate = 1/res.data.routeList[0].path.tick[0].charge
            this.toFromRate = toFromRate
            this.coinNum2 = (this.fromToRate*0.997).toFixed(6)
            this.tradeData1 = ((this.fromToRate*0.997)/this.coinNum1).toFixed(6)
            this.tradeData2 = ((this.toFromRate*0.997)*this.coinNum1).toFixed(6)
            this.priceData1 = Number(res.data.token0Usdt).toFixed(4)
            this.priceData2 = Number((res.data.token0Usdt / this.fromToRate)*this.coinNum1).toFixed(4)
            this.token1_amount = Number(window.web3.utils.fromWei((res.data.routeList[0].path.tick[0][0].token1_amount).toString())).toFixed(6)
            if(Number(this.coinNum2) > Number(this.token1_amount)){
              this.coinNum2 = Number(this.token1_amount).toFixed(6)
              Toast('Current pool maximum convertible：' + this.token1_amount + this.curCoin2)
            }
            // console.log('%%%%%%%%%', token1_amount)
          }
          if(Number(this.coinNum2) > Number(this.token1_amount)){
            this.coinNum2 = Number(this.token1_amount).toFixed(6)
            Toast('Current pool maximum convertible：' + this.token1_amount + this.curCoin2)
            this.coinNum1 = (this.coinNum2 /(this.fromToRate*0.997)).toFixed(6)
          }
        }
        }).catch((err) => {
        console.log(err);
      });
    },
    getMax(){
      this.coinNum1 = this.coinBalance1
      this.getTrade()
    },
    // 获得Token对应交易费率
    getTokkenFee(){
      window.PgoMixContract.methods.getTokenFee(this.cherryAddr)
      .call()
      .then((value) => {
        // console.log(value)
        let chargeRate = Number(value)/1000000
        this.chargeRate = chargeRate
        // console.log(chargeRate)
      }); 
    },
    // 授权前的判断
    beforApprove(){
      if(Number(this.coinNum1) > Number(this.coinBalance1)){
          Toast.fail('Insufficient balance')
      }else if(this.coinNum1 == null){
        this.coinNum1 = "1"
      }else{
        this.setApprove()
      }
      // console.log(this.coinBalance1)
    },
    // 代币地址授权
    async setApprove(){
      this.arrovestatus = true
      let approveAddr = this.curAddr1
      this.approveAddr = approveAddr
      let exchangeNum = window.web3.utils.toWei(this.coinNum1.toString())
      this.exchangeNum = exchangeNum
      window.approveContract = new window.web3.eth.Contract(configabi.Erc20, this.approveAddr)
      // console.log(configabi.PgoMixAddress)
      // console.log(this.exchangeNum)
      let gasAmount = await window.approveContract.methods.approve(configabi.PgoMixAddress, this.exchangeNum).estimateGas({from: window.defaultAccount})
      window.approveContract.methods.approve(configabi.PgoMixAddress, this.exchangeNum)
      .send({
        from:window.defaultAccount,
        gas: gasAmount,
        gasPrice: window.web3.utils.toWei('0.15','gwei')
      })
      .on('confirmation', (confirmationNumber, receipt)=>{
        // console.log({confirmationNumber:confirmationNumber,receipt:receipt}) 
        if(confirmationNumber == 0 ){
        }  
      })
      .on('receipt', (receipt)=>{
        // Toast.clear()
        Toast.success('Authorization succeeded, please exchange!')
        this.approveOn = false
        this.arrovestatus = false
        // console.log({receipt:receipt})
        // this.reload()  // 刷新数据
      })
      .on('error', (error, receipt) =>{
        Toast.fail('Approve failed')
        this.arrovestatus = false
        reject({error:error,receipt:receipt})
      })      
    },
    // 兑换前的判断
    judgeSwap(){
      if(Number(this.coinNum1) > Number(this.coinBalance1)){
        Toast.fail('Insufficient balance')
      }else{
        this.multiSwap()
      }
    },
    // 调用外部交易所聚合兑换
    async multiSwap(){
      this.swapstatus = true
      // console.log(this.chargeRate)
      // console.log('当前滑点',this.sliderCoin)
      let newOutToken = (Number(this.coinNum2) - Number(this.coinNum2)*this.sliderCoin).toString()
      let inToken = this.curAddr1
      this.inToken = inToken
      let amountIn = window.web3.utils.toWei(this.coinNum1)
      this.amountIn = amountIn
      let outToken = this.curAddr2
      this.outToken = outToken
      let minOut = window.web3.utils.toWei(newOutToken)
      this.minOut = minOut
      let myRate = this.coinNum1
      this.myRate = myRate
      // console.log(this.minOut)
      let ratioNum = window.web3.utils.toWei((Number(this.myRate) - (Number(this.myRate)*this.chargeRate)).toString())
      let polymeData = {
        token0: this.inToken,
        token1: [this.outToken],
        routerList: [this.cherryRouter],
        ratio: [ratioNum]
      }
      // console.log('user--',window.defaultAccount)
      // console.log('inToken--',this.inToken)
      // console.log('amountIn--',this.amountIn)
      // console.log('outToken--',this.outToken)
      // console.log('minOut--',this.minOut)
      // console.log('_data--', polymeData)
      
      if(this.curAddr1 == '0x0000000000000000000000000000000000000000'){
        // let gasAmount = await window.PgoMixContract.methods
        // .multiSwap(window.defaultAccount,this.inToken,this.amountIn,this.outToken,this.minOut, polymeData).estimateGas({from: window.defaultAccount});
        // console.log('gasAmount',gasAmount)
        let multiInfor = {
          from: window.defaultAccount,
          gas: 1000000,
          gasPrice: window.web3.utils.toWei(this.swapGas, "gwei"),
          value: this.amountIn
        }
        this.multiInfor = multiInfor
      }else{
        let multiInfor = {
          from: window.defaultAccount,
          gas: 1000000,
          gasPrice: window.web3.utils.toWei(this.swapGas, "gwei"),
        }
        this.multiInfor = multiInfor
      }
      // console.log('-----------Send调用-----------',this.multiInfor)
      try{
        window.PgoMixContract.methods
        .multiSwap(window.defaultAccount,this.inToken,this.amountIn,this.outToken,this.minOut, polymeData)
        .send(this.multiInfor)
        .on("confirmation", (confirmationNumber, receipt) => {
          
        })
        .on("receipt", (receipt) => {
          this.approveOn = true
          this.enterSwap = false
          this.swapstatus = false
          // this.reload()
          this.$refs.walletChild.getCoinInfor()
          // console.log({ receipt: receipt });
        })
        .on("error", (error, receipt) =>{
          this.swapstatus = false
          Toast.fail('Exchange failed')
          reject({ error: error, receipt: receipt })
        });
      }catch(err){
        this.swapstatus = false
        Toast.fail(err)
      }
    },
    coinList1(){
      let e = 1
      this.$refs.walletChild.coinPop1(e)
    },
    coinList2(){
      let e = 2
      this.$refs.walletChild.coinPop2(e)
    },
    chioseCoin1(data){
      // console.log(data)
      if(data.symbol !== this.curCoin2){
        this.selectCoin1 = data.symbol
        this.selectBalance1 = data.balance
        this.selectlogoURI1= data.logoURI
        this.selectAddr1 = data.tokenAddr
        this.curCoin1 = this.selectCoin1
        this.curAddr1 = this.selectAddr1
        this.logoURI1 = this.selectlogoURI1
        this.coinBalance1 = this.selectBalance1
        this.getAddr1()
        this.getTrade()
      }else{
        Toast.fail('Two tokens cannot be duplicated')
      }
    },
    chioseCoin2(data){
      // console.log(data)
      if(data.symbol !== this.curCoin1){
        this.selectCoin2 = data.symbol
        this.selectBalance2 = data.balance
        this.selectlogoURI2= data.logoURI
        this.selectAddr2 = data.tokenAddr
        this.curCoin2 = this.selectCoin2
        this.coinBalance2 = this.selectBalance2
        this.logoURI2 = this.selectlogoURI2
        this.curAddr2 = this.selectAddr2
        this.getAddr2()
        this.getTrade()
      }else{
        Toast.fail('Two tokens cannot be duplicated')
      }
    },
    // 切换
    changeTrade(){
      this.changeOn = !this.changeOn
      if(this.changeOn){
        this.curCoin1 = this.selectCoin1
        this.curCoin2 = this.selectCoin2
        this.coinBalance1 = this.selectBalance1
        this.coinBalance2 = this.selectBalance2
        this.logoURI1 = this.selectlogoURI1
        this.logoURI2 = this.selectlogoURI2
        this.curAddr1 = this.selectAddr1
        this.curAddr2 = this.selectAddr2
        this.getTrade()
      }else{
        this.curCoin1 = this.selectCoin2
        this.curCoin2 = this.selectCoin1
        this.coinBalance1 = this.selectBalance2
        this.coinBalance2 = this.selectBalance1
        this.logoURI1 = this.selectlogoURI2
        this.logoURI2 = this.selectlogoURI1
        this.curAddr1 = this.selectAddr2
        this.curAddr2 = this.selectAddr1
        this.getTrade()
      }
    },
  }
}
</script>
