<template>
  <div class="trade-cont">
    <van-popup v-model="showLine" class="wallet-list" closeable>
      <h4>Select a token</h4>
      <van-search v-model="searchCont" placeholder="Search name or paste adress" />
      <ul v-if="searchResult.length > 0">
        <li @click="curWallet(item,index)" :class="{ 'curActive': isActive }" v-for="(item, index) in searchResult" :key="index">
            <div class="coin-line">
              <img :src="item.logoURI" alt="">
              <span>{{item.symbol}}</span>
            </div>
            <em>{{item.balance}}</em>
        </li>
      </ul>
      <div class="no-data" v-else>No data</div>
    </van-popup>
  </div>
</template>
<script>
import { Popup } from 'vant'
import 'vant/lib/index.css'
import { Search } from 'vant'
import coinInfo from "../../abi/pgoswap" // 所有代币的信息
import NCWeb3 from "../../assets/js/web3"
import { configabi } from '../../abi/configabi'
export default {
  name: '',
  components: { 
    [Popup.name]: Popup,
    [Search.name]: Search, 
  },
  props: {},
  data () {
    return {
      showLine: false,
      searchCont: '',
      lineCoin: 0,
      walletList: [
        
      ],
      searchList:[],
      isActive: false
    } 
  },
  computed: { 
    searchResult(){
      let coinEntryArr = this.walletList
      if(!this.searchCont){
        return coinEntryArr
      }
      this.searchCont = this.searchCont.trim().toLowerCase()
      coinEntryArr = coinEntryArr.filter(item =>{
        if(item.symbol.toLowerCase().indexOf(this.searchCont) !== -1){
          // console.log(item)
          return item;
        }else if(item.tokenAddr.toLowerCase().indexOf(this.searchCont) !== -1){
          // console.log(item)
          return item;
        }
      })
      // if(this.curCoinNum2 == '0x' && this.searchCont.length == 42){
      //   window.balanceContract = new window.web3.eth.Contract(configabi.Erc20,this.searchCont)
      //   window.balanceContract.methods.balanceOf(window.defaultAccount)
      //   .call({from: window.defaultAccount})
      //   .then((value) => {
      //     let localBalance = Number(window.web3.utils.fromWei(value.toString())).toFixed(6)
      //     this.localBalance = localBalance
      //     // console.log('balance：',this.localBalance)
      //   })
      //   window.balanceContract.methods.symbol()
      //   .call({from: window.defaultAccount})
      //   .then((value) => {
      //     let localsymbol = value
      //     this.localsymbol = localsymbol
      //     // console.log('symbol：',value)
      //   })
      //   window.balanceContract.methods.name()
      //   .call({from: window.defaultAccount})
      //   .then((value) => {
      //     let localname = value
      //     this.localname = localname
      //     // console.log('name：',value)
      //   })
      //   setTimeout(() => {
      //     // 0x82Ce2bF9729d92D5B6aa34031B1C7c68CE0adab9
      //     // console.log('balance：',this.localBalance)
      //     let addList = [
      //       {
      //         balance: this.localBalance,
      //         tokenAddr: this.searchCont,
      //         logoURI: require('../../assets/img/coin/coin-any.svg'),
      //         symbol: this.localsymbol,
      //         name: this.localname
      //       }
      //     ]
      //     // console.log(addList)
      //     // console.log(this.walletList)
      //     let newLocalList = [...addList, ...this.walletList]
      //     // console.log(newLocalList)
      //     this.walletList = newLocalList
      //     localStorage.setItem('localList', JSON.stringify(this.walletList))
      //     if(JSON.parse(localStorage.getItem('localList')) !== null){
      //       this.walletList = JSON.parse(localStorage.getItem('localList'))   //获取localStorage数组数据
      //       // console.log(this.walletList)
      //       // console.log([...new Set(this.walletList)])
      //     }
      //   },600)
      // }
      return coinEntryArr
    }
  },
  watch: {},
  created () {
    this.searchList = this.walletList
  },
  mounted () {
    NCWeb3.Init(addr => {
      this.setTrade()
		})
  },
  beforeDestroy() {
      
  },
  methods: {
    addCoinList(){
      window.balanceContract = new window.web3.eth.Contract(configabi.Erc20,this.searchCont)
      window.balanceContract.methods.balanceOf(window.defaultAccount)
      .call({from: window.defaultAccount})
      .then((value) => {
        let localBalance = Number(window.web3.utils.fromWei(value.toString())).toFixed(6)
        // console.log('balance：',localBalance)
        this.localBalance = localBalance
        window.balanceContract.methods.symbol()
        .call({from: window.defaultAccount})
        .then((value) => {
          // console.log('symbol：',value)
          let localsymbol = value
          this.localsymbol = localsymbol
          window.balanceContract.methods.name()
          .call({from: window.defaultAccount})
          .then((value) => {
            // console.log('name：',value)
            let localname = value
            this.localname = localname
            let addList = [
              {
                balance: this.localBalance,
                tokenAddr: this.searchCont,
                logoURI: require('../../assets/img/coin/coin-any.svg'),
                symbol: this.localsymbol,
                name: this.localname
              }
            ]
            // console.log(addList)
            this.walletList = [...addList, ...this.walletList]
            // console.log(this.walletList)
          })
        })
      })
    },
    setTrade(){
      if (typeof window.web3 !== 'undefined') {
        window.PgoMixContract = new window.web3.eth.Contract(configabi.PgoMix.abi,configabi.PgoMixAddress)  
        this.getCoinInfor()   
      } 
    },
    curWallet(e){
      this.showLine = false
      if(this.lineCoin == 1){
        this.$emit('coin-type1', e)
      }else if(this.lineCoin == 2){
        this.$emit('coin-type2', e)
      }
    },
    coinPop1(e){
      this.lineCoin = e 
      this.showLine = true
      // console.log('1111')
    },
    coinPop2(e){
      this.lineCoin = e 
      this.showLine = true
      // console.log('2222')
    },
    async getCoinInfor(){
      let curCoinNum = await web3.eth.getBalance(window.defaultAccount)
      this.curCoinNum = Number(window.web3.utils.fromWei(curCoinNum.toString())).toFixed(6)
      let curCoinNum2 = await web3.eth.getCode(window.defaultAccount)
      this.curCoinNum2 = curCoinNum2
      // console.log(curCoinNum2)
      // console.log(coinInfo.tokens)
      let moneryList = []
      coinInfo.tokens.forEach(item => {
        window.balanceContract = new window.web3.eth.Contract(configabi.Erc20,item.address)
        window.balanceContract.methods.balanceOf(window.defaultAccount)
        .call({from: window.defaultAccount})
        .then((value) => {
          let balance = Number(window.web3.utils.fromWei(value.toString())).toFixed(4)
          // console.log('余额',balance)
          let tokenAddr = item.address
          let logoURI = item.logoURI
          let symbol = item.symbol
          let name = item.name
          moneryList.push({
            balance,
            tokenAddr,
            logoURI,
            symbol,
            name
          })
        });
       }); 
      setTimeout(() => { 
        let oktInfo = [
          {
            balance: this.curCoinNum,
            tokenAddr: '0x0000000000000000000000000000000000000000',
            logoURI:'https://static.oklink.com/cdn/assets/imgs/218/39E0ACE3BABBCE0B.png',
            symbol: 'OKT',
            name: 'OKT'
          }
        ]
        this.moneryList = [...oktInfo,...moneryList]
        // console.log(oktInfo)
        // console.log('***********',[...oktInfo,...moneryList])
        // console.log(this.walletList)
        this.walletList = this.moneryList
        this.searchList = this.moneryList
        localStorage.setItem('localList',JSON.stringify(this.walletList))
        // console.log(this.searchList)
        this.$parent.getInitial()
        this.$parent.getAddr1()
        this.$parent.getAddr2()
        this.$parent.getTrade()
      }, 600)
    }
  }
}
</script>
<style lang="less" scoped>
  .trade-cont{
    .coin-list-line{
      margin: 20px 0;
    }
    .wallet-list{
      max-width: 400px;
      width: 100%;
      z-index: 99;
      border-radius: 0;
      overflow: hidden;
      max-height: 600px;
      min-height: 300px;
      .coin-list{
        position: relative;
      }
      h4{
        font-weight: 600;
        position: relative;
        margin: 20px;
        img{
          width: 20px;
          position: absolute;
          right: 0;
          cursor: pointer;
        }
      }
      /deep/ .van-search{
        padding: 0;
        margin: 0 20px;
      }
      ul{
        overflow-y: auto;
        padding: 0 20px;
        margin: 0 0 30px 0;
        // -webkit-overflow-scrolling:touch;
        // z-index: 100;
        // min-height: 64px;
        max-height: 500px;
        li{
          margin: 30px 0;
          cursor: pointer;
          display: flex;
          align-items: center;
          justify-content: space-between;
          .coin-line{
            display: flex;
            align-items: center;
          }
          span{
            color: #0049dc;
          }
          img{
            width: 20px;
            margin: 0 10px 0 0;
            border-radius: 50%;
          }
        }
      }
      .no-data{
        padding: 30px 0;
        text-align: center;
      }
    }
    @media screen and (max-width: 768px){
      .wallet-list{
        border-radius: 0;
        bottom: 0;
        max-width: 400px;
        transform: translate(-50%, 0);
        height: calc(100vh - 74px);
        ul{
          li{
            max-height: calc(100vh - 174px); 
          }
        }
      }
    }
  }
</style>