<template>
  <div class="trade-cont">
    <div class="trade-cont-tab" v-show="tabStatus1">
      <van-tabs v-model="active">
        <van-tab title="Market"><Market ref="childMarket"/></van-tab>
        <van-tab title="Limit" disabled><Limit ref="childLimit"/></van-tab>
      </van-tabs>
      <div class="icon-menu">
        <img @click="refResh()" src="../../assets/img/icon-refresh.svg" alt="">
        <img @click="setUp()" src="../../assets/img/icon-setting.svg" alt="">
      </div>
    </div>
    <div class="trade-cont-tab" v-show="tabStatus2">
      <div class="icon-setup">
        <img @click="setBack()" src="../../assets/img/icon-back.svg" alt="">
      </div>
      <div class="setup-title">
        Advanced Settings
      </div>
      <Setup ref="setUp"/>
    </div>
  </div>
</template>
<script>
import { Tab, Tabs } from 'vant'
import Market from '../trade/market'
import Limit from '../trade/limit'
import Setup from '../trade/setup'
import 'vant/lib/index.css'
export default {
  name: '',
  components: {
    [Tab.name]:Tab,
    [Tabs.name]:Tabs, 
    Market,
    Limit,
    Setup
  },
  props: {},
  data () {
    return {
      active: 0,
      tabStatus1: true,
      tabStatus2: false
    }
  },
  computed: { 
  },
  watch: {},
  created () {},
  mounted () {
    // console.log(this.$refs.setUp.checkSwap)
    // this.setTime()
  },
  beforeDestroy() {
      
  },
  inject:[ 'reload'],
  methods: {
    setUp(){
      this.tabStatus1 = false
      this.tabStatus2 = true
    }, 
    setBack(){
      this.tabStatus1 = true
      this.tabStatus2 = false
    },
    refResh(){
      // this.reload()
      // this.$emit.childMarket.curCoin1
      this.$refs.childMarket.getTrade()
      // console.log(this.$refs.childMarket)
    },
    setTime(){
      setTimeout(()=>{
		  	this.$refs.childMarket.getTrade()
        console.log('12323')
		  },100)
    },
  }
}
</script>
<style lang="less" scoped>
  .trade-cont{
    .trade-cont-tab{
      position: relative;
      width: 360px;
      margin: 30px auto 0 auto;
      border: 1px #0049dc solid;
      padding: 1rem 30px 2rem 30px;
      border-top: 8px #0049dc solid;
      background: #fff;
    }
    .icon-menu{
      position: absolute;
      top: 30px;
      right: 30px;
      img{
        width: 24px;
        margin: 0 0 0 25px;
        cursor: pointer;
      }
    }
    .icon-setup{
      position: absolute;
      top: 20px;
      img{
        width: 24px;
        cursor: pointer;
      }
    }
    .setup-title{
      text-align: center;
      font-size: 22px;
      color: #0049dc;
    }
  }
  /deep/ .van-tabs__nav{
    background: none;
  }
  /deep/ .van-tab--active{
    color: #0049dc;
  }
  /deep/ .van-tab{
    span{
      font-size: 18px;
    }
  }
  /deep/ .van-tabs__line{
    background: #0049dc;
  }
  /deep/ .van-tabs__wrap{
    margin: 0 0 20px 0;
    width: 240px;
    padding: 0;
  }
  /deep/ .van-tab--disabled{
    color: #7a92e6;
  }
</style>