<template>
  <div class="trade-cont">
    <h5>Slippage tolerance</h5>
    <div class="set-line">
      <van-tabs v-model="setActive1" @click="setupTab1">
        <van-tab title="0.5%" name="0.005"></van-tab>
        <van-tab title="1%" name="0.01"></van-tab>
        <van-tab title="2%" name="0.02"></van-tab>
        <van-tab title="3%" name="0.03"></van-tab>
      </van-tabs>
      <van-field class="field-line" @input= 'changeInput1' v-model="coinNum1" type="number" placeholder="Custom"/>
      <i>%</i>
    </div>
    <h5>Gas Price</h5>
    <div class="set-line">
      <van-tabs v-model="setActive2" @click="setupTab2">
        <van-tab title="Standard"></van-tab>
        <van-tab title="Fast"></van-tab>
        <van-tab title="Instant"></van-tab>
      </van-tabs>
      <van-field class="field-line" @input= 'changeInput2' v-model="coinNum2" type="number" placeholder="Custom"/>
    </div>
    <h5>Liquidity Sources</h5>
    <div class="set-line2">
      <el-checkbox-group v-model="checkSwap" @change ='swapCheck' size="medium" :min="1">
        <!-- <el-checkbox-button v-for="(item, index) in swapList" :label="item" :key="index">{{item}}</el-checkbox-button> -->
        <el-checkbox-button :label="swapList[0]"><img src="../../assets/img/coin/coin-pgo.png"><em>Pgoswap</em></el-checkbox-button>
        <el-checkbox-button :label="swapList[1]"><img src="../../assets/img/coin/coin-che.png"><em>Cherryswap</em></el-checkbox-button>
        <el-checkbox-button :label="swapList[2]"><img src="../../assets/img/coin/coin-kswap.png"><em>Kswap</em></el-checkbox-button>
      </el-checkbox-group>
    </div>
  </div>
</template>
<script>
import { Tab, Tabs } from 'vant'
import { Field } from 'vant'
// import { Checkbox, CheckboxGroup } from 'vant'
import { eventBus } from '../../main'
import 'vant/lib/index.css'
export default {
  name: '',
  components: { 
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [Field.name]: Field,
    // [Checkbox.name]:Checkbox,
    // [CheckboxGroup.name]:CheckboxGroup,
  },
  props: {
   
  },
  data () {
    return {
      setActive1: '',
      setActive2: '',
      coinNum1: '',
      coinNum2: 0,
      checkSwap:[],
      swapList: ['Pgoswap','Cherryswap','Kswap'],
      curGasPrice: '0.15',
      coinSlide:''
    }
  },
  computed: { 
  },
  watch: {},
  created () {
    this.checkSwap = this.swapList
  },
  mounted () {
    
  },
  beforeDestroy() {
      
  },
  methods: {
    setupTab1(name,title){
      this.coinNum1 = ''
      this.coinSlide = name
      // console.log(title)
      eventBus.$emit("curcoinSlide", this.coinSlide)
    },
    setupTab2(name,title){
      if(name ==0){
        this.curGasPrice = '0.15'
      }else if(name ==1){
        this.curGasPrice = '1'
      }else{
        this.curGasPrice = '2'
      }
      eventBus.$emit("curGasPrice", this.curGasPrice)
      // console.log(this.curGasPrice)
    },
    changeInput1(e){
      this.coinNum1 = e
      this.coinSlide = (Number(e)/100).toString()
      eventBus.$emit("curcoinSlide", this.coinSlide)
      // console.log(this.coinSlide)
    },
    changeInput2(e){
      this.curGasPrice = e
      eventBus.$emit("curGasPrice", this.curGasPrice)
    },
    getRate(){
      // console.log(this.$parent.rateList) 
    },
    swapCheck(name){
      this.curSwapList = name
      eventBus.$emit("curSwapList", this.curSwapList)
    }
  }
}
</script>
<style lang="less" scoped>
  .trade-cont{
    position: relative;
    margin: 20px 0 0 0;
    h5{
      color: #0049dc;
      font-size: 16px;
      margin: 35px 0 15px 0;
    }
    .set-line{
      display: flex;
      position: relative;
      justify-content: space-between;
      height: 32px;
      // align-items: center;
      .van-field{
        width: 90px;
        padding: 0 24px 0 10px;
        height: 32px;
        border: 1px #0049dc solid;
        background: #fdeb5e;
        /deep/ .van-cell__value{
          height: 32px;
          line-height: 32px;
          margin: 0 0 0 0;
        }
        /deep/ .van-field__control{
          color: #0049dc;
        }
        input{
          height: 32px;
          line-height: 32px;
        }
      }
      i{
        position: absolute;
        z-index: 5;
        right: 10px;
        top: 7px;
        color: #0049dc;
      }
  }
    /deep/ .van-tab{
      span{
        font-size: 14px;
      }
    }
    /deep/ .van-tabs__line{
      height: 0;
    }
    /deep/ .van-tabs{
      .van-tabs__nav--line{
        height: 32px;
        line-height: 32px;
        margin: 0 0 0 1px;
      }
      .van-tab{
        border: 1px #0049dc solid;
        // border-right: none;
        margin-left: -1px;
        padding: 0;
        color: #0049dc;
      }
      .van-tab--active{
        background: #0049dc;
        color: #fff;
      }
    }
    .set-line2{
      .el-checkbox-group{
        width: 100%;
         .el-checkbox-button{
          img{
            width: 20px;
            border-radius: 10px;
            margin: 0 5px 0 0;
          }
          .el-checkbox-button__inner{
            border-radius: 10px;
          }
        }
        // .el-checkbox-button:first-child .el-checkbox-button__inner{
        //   border-radius: 10px;
        // }
      }
    }
  }
</style>
<style>
  .set-line2 .el-checkbox-button .el-checkbox-button__inner{
    border-radius: 0;
    margin: 0 10px 10px 0;
    padding: 5px 10px;
    border: 1px solid #0049dc;
    border-left: 1px solid #0049dc;
    display: flex;
    align-items: center;
  }
  .set-line2 .el-checkbox-button.is-checked .el-checkbox-button__inner{
    background: #0049dc;
    box-shadow: none;
  }
  .set-line2 .el-checkbox-button.is-checked:first-child .el-checkbox-button__inner{
    border: 1px #0049dc solid;
  }
  .set-line2 .el-checkbox-button.is-disabled .el-checkbox-button__inner{
    color: #fff;
  }
</style>